<template>
  <div>
    <AddAnime />
    <h1>Animes</h1>
    <div class="animes">
        <div 
        v-for="anime in allAnimes"
        :key="anime.id"
        :id="anime.id"
        class="anime"
        :class="{'assistido': anime.assistido}">

        <router-link tag="h3" :to="{ name: 'AnimeDetail', params: { id: anime.id } }">
          {{anime.title}}
        </router-link>
        

        <b-iconstack font-scale="1" variant="white"  @click="viuAnime(anime)" v-if="anime.assistido == false">
          <b-icon stacked icon="square"></b-icon>
          <b-icon stacked icon="check"></b-icon>
        </b-iconstack>
        <b-icon @click="deleteAnime(anime.id)" variant="white" icon="trash" font-scale="1"  v-if="anime.assistido == true"></b-icon>

        </div>
    </div>
      
  </div>
</template>
<script>

import { mapGetters, mapActions } from "vuex";
import AddAnime from "./AddAnime"

export default {
  name: 'AnimeLista',
  components: {
    AddAnime
  },
  methods: {
    ...mapActions(["getAnimes", "deleteAnime",  "updateAnime"]),
    viuAnime(anime){
      anime.assistido = true
      this.updateAnime(anime)
    }
  },
  computed: mapGetters(["allAnimes"]),
  created() {
    this.getAnimes();
  }
}
</script>

<style scoped>
.animes {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
}
.anime {
  border: 1px solid #ccc;
  background: #41b883;
  padding: 1rem;
  border-radius: 5px;
  text-align: center;
  position: relative;
  cursor: pointer;
}
b-icon {
  position: absolute;
  bottom: 10px;
  right: 10px;
  cursor: pointer;
  color: #fff;
}
.legend {
  display: flex;
  justify-content: space-around;
  margin-bottom: 1rem;
}
.complete-box {
  display: inline-block;
  width: 10px;
  height: 10px;
  background: #35495e;
}
.incomplete-box {
  display: inline-block;
  width: 10px;
  height: 10px;
  background: #41b883;
}
.assistido {
  background: #35495e;
  color: #fff;
}
@media (max-width: 500px) {
  .animes {
    grid-template-columns: 1fr;
  }
}
</style>
