<template>
  <div>
    <b-form inline>
    <label class="sr-only" for="inline-form-input-name">Anime</label>
    <b-input cols="8" v-model="anime_add.title"  id="inline-form-input-name" placeholder="Anime"
    ></b-input>
  <b-button cols="4" @click="onSubmit()">Save</b-button>
  </b-form>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  name: "AddAnime",
  data() {
    return {
      anime_add :{
        title: ""
      }
    };
  },
  methods: {
    ...mapActions(["addAnime"]),
    onSubmit() {
      this.addAnime(this.anime_add);
    }
  }
};
</script>

