import axios from "axios";

const state = {
    animes:[]
  };
  
  const getters = {
    allAnimes: state => state.animes,
    animeById: (state) => (id) => (state.animes.filter(t => t.id == id))[0],
  };

  const actions = {
    getAnimes({ commit }) {
        axios.get(
            "https://my-json-server.typicode.com/elianpk/fakeapi/animes/"
          ).then((response) => {
            commit('getAnimes', response.data);
        });
    },

    deleteAnime({ commit }, id) {
        commit("removeAnime", id);
    },

    addAnime({ commit }, anime_add) {
      const new_index =  state.animes.length +1
      const response =  {
              "id": new_index ,
              "title": anime_add.title,
              "img": anime_add.img,
              "assistido": false,
            }
      commit("newAnime",response);
    },

    updateAnime({ commit }, updAnime) {
      commit("updateAnime", updAnime);
    },
    
  };
  const mutations = {
    getAnimes: (state, animes) => (state.animes = animes),
    removeAnime: (state, id) => (state.animes = state.animes.filter(t => t.id !== id)),
    newAnime: (state, anime) => state.animes.unshift(anime),

    updateAnime: (state, updAnime) => {
      const index = state.animes.findIndex(t => t.id === updAnime.id)
      if (index !== -1) {
        state.animes.splice(index, 1, updAnime);
        }
      }
  };
 
  export default {
    state,
    getters,
    actions,
    mutations,
  };
  