import Vue from 'vue';
import VueRouter from 'vue-router';
import AnimeLista from './components/Animes/AnimeLista'
import AnimeDetail from './components/Animes/AnimeDetail'


Vue.use(VueRouter);

export default new VueRouter({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'index',
            component: AnimeLista
        },
        {
            path: '/animes',
            name: 'AnimeList',
            component: AnimeLista
        },
        {
            path: '/anime/:id',
            name: 'AnimeDetail',
            component: AnimeDetail
        },
    ]
});
